.sidebar {
  background-color: rgba(0, 0, 0, 0.888);
  color: white;
  min-height: 100vh;
  width: 20%;
  /* margin: 20px 0px;
  margin-left: 15px; */
}
.outer-container .right-panel {
  background-color: white;
  min-height: 100vh;
  width: 79%;
}
.outer-container .right-panel header {
  margin-bottom: 15px;
  border: 1px solid;
  height: 60px;
  /* box-shadow: 1px 1px 3px rgb(234, 218, 218); */
}
.outer-container .right-panel .homepage {
  min-height: 90%;
  border: 1px solid;
}
.outer-container .sidebar p {
  margin: 30px 15px;
}
.outer-container .sidebar .menu {
  margin-top: 100px;
}

.outer-container .sidebar i,
a {
  font-size: 20px;
  margin-right: 15px;
  color: white;
  text-decoration: none;
}

